.seeingDiv{
    display: flex;
    margin: 20px 10px 20px 10px;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;

    /*No selectable Elements*/
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}

.seeingBlock{
    width: 15vw;
    min-width: 100px;
    background: #000;
    border-radius: 5px;
    box-shadow: 2px 2px 5px #007;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    z-index: 75;
}

.seeingImg{
    width: 100%;
    height: 100%;
}

.seeingImg:hover{
    cursor: pointer;
}

.Watching{
    margin-top: 100px;
}

.Watching h3{
    font-size: 30px;
    color: #fff;
    text-align: center;
    text-shadow: 2px 2px 5px #007;
}

.spaceTop{
    margin-top: 50px;
}

@media screen and (max-width: 600px)
{
    .Watching h3{
        font-size: 20px;
    }
}